import {
  AfterViewChecked,
  ChangeDetectorRef,
  Component,
  OnInit,
} from '@angular/core';
import {
  ActivatedRouteSnapshot,
  Router,
  RouterStateSnapshot,
  UrlTree,
} from '@angular/router';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { Observable } from 'rxjs';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import {
  VhBookingInvoiceDetail,
  VhBookingInvoices,
} from 'src/app/cafe/interface/vh-booking';
import { BookingA4Component } from 'src/app/cafe/components/print/booking/booking-a4/booking-a4.component';
import { BookingK57Col3Component } from 'src/app/cafe/components/print/booking/booking-k57-col3/booking-k57-col3.component';
import { BookingK80Col3Component } from 'src/app/cafe/components/print/booking/booking-k80-col3/booking-k80-col3.component';
import { BookingK80Col4Component } from 'src/app/cafe/components/print/booking/booking-k80-col4/booking-k80-col4.component';
import { BookingA5Component } from 'src/app/cafe/components/print/booking/booking-a5/booking-a5.component';
@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss'],
})
export class CartComponent implements OnInit {
  dataRestore: any;
  invoices: VhBookingInvoices = new VhBookingInvoices({}, []);
  // thng tin bn ang c t
  tableInfo: string = '';
  // trng thi
  statusInvoice: any = {
    updating: false,
    deleting: false,
  };
  // thuc tnh my in
  printer: any;

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private router: Router,
    public vhAuth: VhAuth,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService
  ) {
    let dataTrans: any = this.router.getCurrentNavigation().extras.state;
    this.initInvoice(dataTrans);
  }

  ngOnInit(): void {}

  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }
  /**Pop up bo khi pht sinh li */
  alertErr(err) {
    console.error(err);
    this.vhComponent.showToast(2000, 'Li kt ni', 'alert-toast');
  }

  /**Render food name */
  renderNameFood(id_prod: string, id_subProd?: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod);
    let subFood: any;

    if (id_subProd) subFood = food.subs.find((item) => item._id == id_subProd);
    return `${food ? food.name : ''} ${
      subFood ? '(' + subFood.name + ')' : ''
    }`;
  }
  /**Render cc thnh phn combo */
  renderSubCombo(arr: any[]) {
    let subName: string = '';
    arr.forEach((item) => {
      if (subName.length) subName += `, ${item.quantity} ${item.name}`;
      else subName += `${item.quantity} ${item.name}`;
    });
    return subName;
  }
  /**Khi to ha n */
  initInvoice({ table, dataRestore }) {
    // ly thu
    let tax: any = this.vhQueryCafe
      .getlocalTaxs()
      .find((tax) => tax.status && tax.default);
    if (tax) this.invoices.setTax(parseFloat(tax.value));

    if (table)
      this.tableInfo = `${
        this.vhQueryCafe.getlocalArea(table.id_area).name
      } - ${table.table_name}`;

    if (dataRestore) this.dataRestore = dataRestore;

    this.invoices.setIdTable(table.id_table);
    this.invoices.setIdArea(table.id_area);
    this.invoices.setIdEmployee(this.vhAuth.getUser()._id);
    this.invoices.setIdBranch(this.vhQueryCafe.getDefaultBranch()._id);
    this.invoices.setDate(table['date']);
    this.invoices.setNote(this.tableInfo);
  }
  /**Save ha n */
  createProcess() {
    console.log(
      this.invoices.getCreateUpdateInvoice(17),
      this.invoices.getCreateUpdateInvoiceDetail()
    );

    return this.vhQueryCafe.createBill_Billdetail(
      this.invoices.getCreateUpdateInvoice(17),
      this.invoices.getCreateUpdateInvoiceDetail()
    );
  }
  completeInvoice(print: boolean) {
    this.statusInvoice['creating'] = true;
    this.createProcess().then(
      (res) => {
        console.log('createProcess', res);

        this.vhComponent.showToast(
          2000,
          `${this.languageService.translate(
            'Booking'
          )} ${this.invoices.getBillCode()} ${this.languageService.translate(
            'has been created successfully'
          )}`,
          'success-toast'
        );
        this.statusInvoice['creating'] = false;
        if (print) this.handlePrint();
        else this.handle();
      },
      (error) => {
        console.log('error', error);
      }
    );
  }

  private checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales');
      if (
        printer &&
        printer.print_sizes[
          printer.print_sizes.findIndex(
            (e) => e._id == printer.print_size_default
          )
        ].enable
      )
        resolve(
          printer.print_sizes[
            printer.print_sizes.findIndex(
              (e) => e._id == printer.print_size_default
            )
          ]
        );
      else rejects('No');
    });
  }

  private renderPrintPage(
    type:
      | 'print_size_a4'
      | 'print_size_a5'
      | 'print_size_k80_4c'
      | 'print_size_k80_3c'
      | 'print_size_k57_3c'
  ) {
    switch (type) {
      case 'print_size_k57_3c':
        return BookingK57Col3Component;
      case 'print_size_k80_3c':
        return BookingK80Col3Component;
      case 'print_size_k80_4c':
        return BookingK80Col4Component;
      case 'print_size_a5':
        return BookingA5Component;
      case 'print_size_a4':
        return BookingA4Component;
      default:
        return BookingA4Component;
    }
  }

  handle() {
    this.exitPage = true;
    this.router.navigate(['/cafe/cafe-pos/booking'], { state: this.dataRestore });
  }
  handlePrint() {
    this.checkPrint().then(
      (printer) => {
        if (printer)
          this.vhComponent
            .showModal(
              this.renderPrintPage(printer['_id']),
              { printer, invoice: this.invoices },
              false,
              false,
              `modal-print-${printer['_id']}`
            )
            .then((modal) => {
              modal.onWillDismiss().then(() => this.handle());
            });
      },
      (err) => this.handle()
    );
  }

  ///////////////////////////////////////////////GO BACK////////////////////////////////////////////////////
  exitPage: boolean = false;
  popUpExit() {
    return new Promise<{ save: boolean; exit: boolean }>((resolve) => {
      const modal: NzModalRef = this.modal.create({
        nzTitle: this.languageService.translate('Confirm'),
        nzContent:
          this.languageService.translate(
            'Are you sure to cancel the sales invoice'
          ) + '?',
        nzFooter: [
          {
            label: this.languageService.translate('Cancel'),
            onClick: () => {
              modal.destroy();
              resolve({ save: false, exit: false });
            },
          },
          {
            label: 'OK',
            type: 'primary',
            onClick: () => {
              modal.destroy();
              resolve({ save: false, exit: true });
            },
          },
        ],
      });
    });
  }
  goBack(event) {
    this.popUpExit().then(({ save, exit }) => {
      this.exitPage = exit;
      if (exit) {
        this.router.navigate(['/cafe/cafe-pos/booking'], {
          state: { dataRestore: this.dataRestore },
        });
      }
    });
  }

  checkDeactivate(
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    if (!this.exitPage) {
      this.popUpExit().then(({ save, exit }) => {
        this.exitPage = exit;
        if (exit) {
          if (nextState.url != '/cafe-pos/booking')
            this.router.navigate([nextState.url]);
          else
            this.router.navigate(['/cafe/cafe-pos/booking'], {
              state: { dataRestore: this.dataRestore },
            });
        }
      });
    }
    return this.exitPage;
  }
}
