import { Component, EventEmitter, HostListener, Input, OnInit, Output } from '@angular/core';
import { VhBookingInvoices } from 'src/app/cafe/interface/vh-booking';

@Component({
  selector: 'app-payment-button1',
  templateUrl: './payment-button1.component.html',
  styleUrls: ['./payment-button1.component.scss']
})
export class PaymentButton1Component {
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F1", "F2"].includes(event.key)) {
      switch (event.key) {
        case "F1": document.getElementById("complete-and-print").click(); break;
        case "F2": document.getElementById("complete").click(); break;
      }
      event.preventDefault()
      event.stopPropagation()
    }
  }
  @Input() invoices: VhBookingInvoices
  @Input() statusInvoice: any = {}
  @Output() complete: EventEmitter<any> = new EventEmitter()
  constructor() { }

  completeInvoice(status: boolean) {
    this.complete.emit(status)
  }

}
