import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { VhBookingInvoices } from 'src/app/cafe/interface/vh-booking';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-bill1',
  templateUrl: './bill1.component.html',
  styleUrls: ['./bill1.component.scss']
})
export class Bill1Component implements OnInit, AfterViewInit {
  @Input() invoices: VhBookingInvoices
  @Input() tax: number = 0
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ]
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);

  moneyEdit: any = {}
  /**bin lu thng tin bill - discount ,fee, payment, cash, note- dng  gn gi tr khi dng bn phim o */
  invoice_discount: string='';
  invoice_fee: string='';
  invoice_cash: string='';
  invoice_note: string='';
  constructor(
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    public vhAuth:VhAuth
  ) { }

  ngOnInit(): void {
    // ly thng tin ti khon
    this.getWallets()
  }

  ngAfterViewInit() {
    // Cleave js
    if(document.querySelector(".discount-price")) this.moneyEdit['discount'] = this.vhAlgorithm.vhnumeral(".discount-price")
    if(document.querySelector(".fee-price")) this.moneyEdit['fee'] = this.vhAlgorithm.vhnumeral(".fee-price")
    if(document.querySelector(".cash-price")) this.moneyEdit['cash'] = this.vhAlgorithm.vhnumeral(".cash-price")
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true }
      })
  }

  /**Cp nht gim gi */
  updateDiscount() {
    let discount: any = this.moneyEdit['discount'].getRawValue()
    this.invoices.setDiscount(discount ? parseFloat(discount) : 0)
  }
  /** Cp nht ph */
  updateFee() {
    let fee: any = this.moneyEdit['fee'].getRawValue()
    this.invoices.setFee(fee ? parseFloat(fee) : 0)
  }


   /** Cp nht tien khach tr */
   updateCash() {
    let cash: any = this.moneyEdit['cash'].getRawValue()
    // this.invoices.setCash(cash ? parseFloat(cash) : 0);
    console.log(cash);

  }
  /**Cp nht thu */
  updateTax(value) {
    this.invoices.setTax(parseFloat(value))
  }
  /**Cp nht ghi ch */
  updateNote(note) {
    this.invoices.setNote(note)
  }

    /**
   * kim tra ti a 12 ky t cu discount
   * @param value
   */
     checkMaxLengthDiscount(value: string) {
      if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_discount = value }
    }

    /**
     * kim tra ti a 12 ky t cu fee
     * @param value
     */
    checkMaxLengthFee(value: string) {
      if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_fee = value }
    }
    /**
     * kim tra ti a 12 ky t cu fee
     * @param value
     */
     checkMaxLengtCash(value: string) {
      if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_cash = value }
    }
}
