import { LanguageService } from 'src/app/cafe/services/language.service';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search-topping',
  templateUrl: './search-topping.component.html',
  styleUrls: ['./search-topping.component.scss'],
})
export class SearchToppingComponent implements OnInit {
  @Input('showDrawerSideDishesList') showDrawerSideDishesList: boolean = true;
  @Input() default_price_type: number;

  @Output('closeSearchSideDishesList')
  closeSearchSideDishesList: EventEmitter<any> = new EventEmitter();

  public sideDishesList: any[] = [];
  choiceSideDishes: any = {};
  public searchList: any[] = [];
  indexTab: number = 0;
  list_menu: any = [];
  listTabs: Array<any> = [
    { _id: 'all', name: this.languageService.translate('All'), type: 1 },
  ];
  // bien kim tra show bn phm o hay ko
  show_keyboard: boolean = this.vhAuth.localStorageGET('show_keyboard');
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhAuth: VhAuth,
    private languageService: LanguageService
  ) {}
  ngOnInit(): void {
    this.sideDishesList = this.vhQueryCafe
      .getlocalProducts()
      .filter((item) => !item.delete_hidden && item.type == 4)
      .map((e) => {
        return { ...e, ...e.units[0] };
      });
    this.sideDishesList = this.vhAlgorithm.sortVietnamesebyASC(
      this.sideDishesList,
      'name'
    );

    this.list_menu = this.vhQueryCafe
      .getlocalCategorys()
      .filter((item) => item.type == 4)
      .map((e) => {
        e.status = this.sideDishesList.find((pro) => pro.id_category == e._id)
          ? true
          : false;
        return { ...e };
      });
    this.listTabs = [
      ...this.listTabs,
      ...this.list_menu.filter((cate) => cate.status),
    ];
    this.searchList = this.sideDishesList;
  }
  ngAfterViewInit() {
    document.getElementById('inputSearchSideDishes').focus();
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index;
  }

  goBack() {
    this.closeSearchSideDishesList.emit(false);
  }

  tempVal: string = '';
  searchSideDishes(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());

    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(
        tempVal,
        this.sideDishesList,
        ['name']
      );
    else this.searchList = this.sideDishesList;
  }
  selectSideDishes(value) {
    this.choiceSideDishes = {
      ...value,
      quantity: 1,
      note: '',
      price_origin: value.price,
      price: value.price,
    };

    if (this.choiceSideDishes) {
      this.showOptions = true;
      setTimeout(() => {
        this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(
          '.price-' + value._id
        );
      }, 200);
    }
  }

  editPrice: any = {};
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue();
    this.choiceSideDishes.price = price ? parseFloat(price) : 0;
    this.choiceSideDishes.quantity =
      this.choiceSideDishes.quantity > 0 ? this.choiceSideDishes.quantity : 1;
  }

  chooseSideDishes() {
    this.showOptions = false;
    if (this.choiceSideDishes['quantity']) {
      this.closeSearchSideDishesList.emit(this.choiceSideDishes);
    }
  }

  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }

  changeTypeGood(event) {
    this.indexTab = event;
    let products = this.vhQueryCafe
      .getlocalDetailProducts()
      .filter((item) => !item.delete_hidden && item.type == 4)
      .map((item) => {
        let unit = this.vhQueryCafe.getUnit_byRatio(
          item.units,
          item.units.find((u) => u.default).ratio
        );
        return { ...item, ...unit };
      });
    if (event == 0) {
      this.sideDishesList = products;
    } else {
      this.sideDishesList = products.filter(
        (item) =>
          item.id_category == this.listTabs[event]._id && !item.delete_hidden
      );
    }
    this.searchSideDishes(this.tempVal);
  }

  /** Tng s lng sn phm */
  increaseQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity + 1;
  }
  /** Gim s lng sn phm */
  reduceQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity - 1;
  }
}
