import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
@Component({
  selector: 'app-search-product',
  templateUrl: './search-product.component.html',
  styleUrls: ['./search-product.component.scss'],
})
export class SearchProductComponent implements OnInit {
  @Input('showDrawerProductList') showDrawerProductList: boolean = true;
  @Input() default_price_type: number;
  @Output('closeSearchProductList') closeSearchProductList: EventEmitter<any> =
    new EventEmitter();
  public foodList: any[] = [];
  public searchList: any[] = [];
  public choiceFood: any = {};
  list_menu: any = [];
  listTabs: Array<any> = [
    { _id: 'all', name: this.languageService.translate('All'), type: 1 },
  ];
  // bien kim tra show bn phm o hay ko
  show_keyboard: boolean = this.vhAuth.localStorageGET('show_keyboard');
  indexTab: number = 0;
  showOptions: boolean = false;
  tempVal: string = '';
  id_branch: string = this.vhQueryCafe.getDefaultBranch()._id;
  auto_hidden_lot: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .auto_hidden_lot;
  auto_select_lot: boolean =
    this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch')
      .auto_select_lot;
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhAuth: VhAuth,
    private languageService: LanguageService
  ) {}
  ngOnInit(): void {
    this.getMenu();
  }
  ngAfterViewInit() {
    document.getElementById('inputSearchDishes').focus();
  }
  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index;
  }
  goBack() {
    this.closeSearchProductList.emit(false);
  }

  getMenu() {
    let products = this.vhQueryCafe
      .getlocalDetailProducts()
      .filter((item) => item.type == 1 || item.type == 3)
      .filter((item) => !item.delete_hidden)
      .map((item) => {
        let unit = this.vhQueryCafe.getUnit_byRatio(
          item.units,
          item.units.find((u) => u.default).ratio
        );
        return { ...item, ...unit };
      });
    this.foodList = products;

    this.list_menu = this.vhQueryCafe
      .getlocalCategorys()
      .filter((item) => item.type == 1 || item.type == 3)
      .map((e) => {
        e.status = products.find((pro) => pro.id_category == e._id)
          ? true
          : false;
        return { ...e };
      });
    this.listTabs = [
      ...this.listTabs,
      ...this.list_menu.filter((cate) => cate.status),
    ];
    this.searchList = this.foodList;
  }

  searchFood(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());

    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.foodList, [
        'name',
      ]);
    else this.searchList = [...this.foodList];
  }

  chooseProduct(item) {
    this.closeSearchProductList.emit(item);
  }

  /** Chn mn n  x l
   * @param value mn n c chn
   */
  selectFoods(value) {
    this.choiceFood = {
      toppings: JSON.parse(JSON.stringify(value.toppings || [])),
      quantity: 1,
      note: '',
      price_origin: value.price,
      name: value.name,
      ptype: value.type,
      properties: value.properties,
      _id: value._id,
      price: value.price,
      ratio: 1,
      units: value.units,
    };
    if (value.lot_number) this.choiceFood['lot_number'] = value.lot_number;
    if (value.lots) this.choiceFood['lots'] = value.lots;
    if (value.id_lotproduct)
      this.choiceFood['id_lotproduct'] = value.id_lotproduct;
    if (value.id_subproduct)
      this.choiceFood.id_subproduct = value.id_subproduct;
    // Lu tr cc properties  chon
    if (this.choiceFood.properties) {
      // sao chp mng gi tr chn
      this.choiceFood.properties.forEach((element) => {
        element.active = true;
        element.selectedValue = element.value[0];
      });
    }
    // Kim tra topping
    if (this.choiceFood.toppings) {
      for (let topping of this.choiceFood.toppings) {
        let data = this.vhQueryCafe.getlocalDetailProduct(topping.id_product);
        topping.quantity = topping.quantity;
        topping.name = data.name;
        topping.price_origin = data.units[0].price;
        topping.price = 0;
        topping.ratio = 1;
        topping.ptype = 4;
        topping._id = topping.id_product;
      }
    }
    if (this.choiceFood.ptype == 3 && this.choiceFood.lots) {
      this.choiceFood.lots = this.vhAlgorithm.sortDatebyASC(
        this.choiceFood.lots,
        'date_exp'
      );
      if (this.auto_hidden_lot)
        this.choiceFood.lots = this.choiceFood.lots.filter(
          (e) => new Date(e.date_exp).getTime() >= new Date().getTime()
        );

      let min_date_exp = this.vhAlgorithm.sortDatebyASC(
        this.choiceFood.lots.filter(
          (e) => new Date(e.date_exp).getTime() >= new Date().getTime()
        ),
        'date_exp'
      );

      if (min_date_exp.length) {
        this.choiceFood['id_lotproduct'] = min_date_exp[0]._id;
        this.choiceFood['lot_number'] = min_date_exp[0].lot_number;
      }
    }

    if (this.choiceFood) {
      this.showOptions = true;
      setTimeout(() => {
        this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(
          '.price-' + value._id
        );
      }, 200);
    }
  }

  editPrice: any = {};
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue();
    this.choiceFood.price = price ? parseFloat(price) : 0;
    this.choiceFood.quantity =
      this.choiceFood.quantity > 0 ? this.choiceFood.quantity : 1;
  }

  selectFoodsCancel() {
    this.showOptions = false;
  }

  chooseFood() {
    this.showOptions = false;
    // if (this.choiceFood.toppings)
    //   this.choiceFood.toppings = this.choiceFood.toppings.filter(
    //     (item) => item.quantity
    //   );
    if (this.choiceFood.quantity)
      this.closeSearchProductList.emit(this.choiceFood);
  }

  changeTypeGood(event) {
    this.indexTab = event;
    let products = this.vhQueryCafe
      .getlocalDetailProducts()
      .filter((item) => item.type == 1 || item.type == 3)
      .filter((item) => !item.delete_hidden)
      .map((item) => {
        let unit = this.vhQueryCafe.getUnit_byRatio(
          item.units,
          item.units.find((u) => u.default).ratio
        );
        return { ...item, ...unit };
      });
    if (event == 0) {
      this.foodList = products;
    } else {
      this.foodList = products.filter(
        (item) =>
          item.id_category == this.listTabs[event]._id && !item.delete_hidden
      );
    }
    this.searchFood(this.tempVal);
  }

  changeUnit(ev, item) {
    if (this.default_price_type == 1) {
      let unit = this.vhQueryCafe.getUnit_byRatio(item.units, ev);
      if (unit) {
        item.unit = unit.unit;
        item.price = parseFloat(unit.price);
        item.ratio = parseFloat(unit.ratio);
        item.price_origin = parseFloat(unit.price);
      }
    } else {
      let unit = this.vhQueryCafe.getUnit_byRatio(item.units, ev);
      if (unit) {
        item.unit = unit.unit;
        item.price = parseFloat(unit.price2);
        item.ratio = parseFloat(unit.ratio);
        item.price_origin = parseFloat(unit.price2);
      }
    }
  }
  changeLot(e) {
    this.choiceFood.lot_number = this.choiceFood.lots.find(
      (item) => item._id == e
    ).lot_number;
  }

  /** Tng s lng sn phm */
  increaseQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity + 1;
  }
  /** Gim s lng sn phm */
  reduceQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity - 1;
  }
}
