import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhQueryCafe, VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhBookingInvoices } from 'src/app/cafe/interface/vh-booking';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-customer1',
  templateUrl: './customer1.component.html',
  styleUrls: ['./customer1.component.scss']
})
export class Customer1Component implements OnInit, OnChanges {
  @Input() invoices: VhBookingInvoices;
  @Input() customerID: string;
  customerList: any[] = []
  customerSearchList = []
  customerType: 1 | 2;

  customer: any = '';
  // Customer
  showDrawerAddCustomer: boolean = false
  showDrawerCustomerInfo: boolean = false
  show_box_search = false 
  showDrawerAddRetail: boolean = false
  constructor(
    public vhQueryCafe: VhQueryCafe,
    private vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private nzModalService: NzModalService,
    private lang: LanguageService
  ) { }

  ngOnInit(): void {
    this.initCustomer()
  }

  initCustomer() {
    this.customerList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
    if (this.customerID == "id_retail") {
      this.customerType = 1
    } else {
      this.customerType = 2;
      this.customer = this.vhQueryCafe.getlocalCustomer(this.customerID).name + ' - ' + this.vhQueryCafe.getlocalCustomer(this.customerID).phone;
    }
  }

  ngOnChanges({ customerID }: SimpleChanges) {
    if (customerID)
      if (customerID.previousValue) {
        if (customerID.previousValue != customerID.currentValue) {
          if (this.customerID == "id_retail") {
            this.customerType = 1
          } else {
            this.customerType = 2;
            this.customer = this.vhQueryCafe.getlocalCustomer(this.customerID).name + ' - ' + this.vhQueryCafe.getlocalCustomer(this.customerID).phone;
          }
        }
      }
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }
  changeCustomerType(type) {
    if (type == 1) {
      this.customerID = "id_retail"
      this.invoices.setIdPartner("id_retail")
      this.invoices.setTaxBuyerDisplayName('Ngi mua khng ly ho n');
      this.invoices.setTaxBuyerLegalName('');
      this.invoices.setTaxBuyerTaxCode('');
      this.invoices.setTaxBuyerAddressLine('');
      this.invoices.setTaxBuyerEmail('');
    } else if (type == 2 && this.invoices.getRetailName()) {
      this.nzModalService.confirm({
        nzTitle: `${this.lang.translate('Thng tin khch l s b mt')}?`,
        nzCancelText: this.lang.translate("Cancel"),
        nzOkText: "Ok",
        nzOnOk: () => {
          this.clearRetailCustomer()
          this.customerList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
          this.customerSearchList = this.customerList
          setTimeout(() => document.getElementById("select-customer").click(), 200)
        },
        nzOnCancel: () => { this.customerType = 1 },
      })
    } else {
      this.customerList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
      this.customerSearchList = this.customerList
      setTimeout(() => document.getElementById("select-customer").click(), 200)
    }
  }
  clearRetailCustomer() {
    this.invoices.setRetailName('')
    this.invoices.setRetailPhone('')
    this.invoices.setRetailAddress('')
  }
  blurCustomer() {
    setTimeout(() => {
      if (this.customerID == "id_retail") this.customerType = 1;
    }, 500)
  }
  changeCustomer(ev) {
    this.invoices.setIdPartner(ev)
    const localCustomer = this.vhQueryCafe.getlocalCustomer(ev);
    this.invoices.setTaxBuyerDisplayName(localCustomer?.name || '');
    this.invoices.setTaxBuyerLegalName(localCustomer?.name || '');
    this.invoices.setTaxBuyerTaxCode(localCustomer?.taxcode || '');
    this.invoices.setTaxBuyerAddressLine(localCustomer?.address || '');
    this.invoices.setTaxBuyerEmail(localCustomer?.email || '');
  }
  // Customer process
  openAddCustomer() {
    if (this.customerType === 1) this.showDrawerAddRetail = true
    else this.showDrawerAddCustomer = true
  }
  closeAddCustomer(ev: boolean) {
    if (ev) { // restart customer list
      this.customerList = this.vhQueryCafe.getlocalCustomers().filter(item => item._id != 'id_retail');
    }
    this.showDrawerAddCustomer = false
  }
  openCustomerInfo() {
    this.showDrawerCustomerInfo = true
  }
  closeCustomerInfo(ev) {
    this.showDrawerCustomerInfo = false
  }

  searchCustomer(event) {
    this.customerList = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(event.toString().toLowerCase()), this.customerSearchList, ['name', 'phone'])
  }
  //Lu thng tin khch l
  saveRetailInfo(result) {
    if (result) {
      this.invoices.setRetailName(result.retail_name)
      this.invoices.setRetailPhone(result.retail_phone)
      this.invoices.setRetailAddress(result.retail_address)
    }
    this.showDrawerAddRetail = false
  }
}
