import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { CanLeavePage } from 'src/app/cafe/desktop/canLeavePage';
import { CartComponent } from './cart.component';
import { BillDetail1Module } from '../components/bill-detail1/bill-detail1.module';
import { Customer1Module } from '../components/customer1/customer1.module';
import { InfoBill1Module } from '../components/info-bill1/info-bill1.module';
import { Bill1Module } from '../components/bill1/bill1.module';
import { PaymentButton1Module } from '../components/payment-button1/payment-button1.module';


const routes: Routes = [
    {
        path: "",
        component: CartComponent,
        canDeactivate: [CanLeavePage]
    }
]

@NgModule({
    declarations: [CartComponent],
    imports: [
        CommonModule,
        FormsModule,
        NgZorroAntModule,
        TranslateModule,
        RouterModule.forChild(routes),
        BillDetail1Module,
        Customer1Module,
        InfoBill1Module,
        Bill1Module,
        PaymentButton1Module
    ]
})
export class CartModule { }