import { Component, Input, OnInit } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';
import { VhBookingInvoices } from 'src/app/cafe/interface/vh-booking';

@Component({
  selector: 'app-info-bill1',
  templateUrl: './info-bill1.component.html',
  styleUrls: ['./info-bill1.component.scss']
})
export class InfoBill1Component implements OnInit {
  @Input() invoices: VhBookingInvoices;
  @Input() employeeID: string;
  @Input() tableInfo: any = {};

  employeeList: any[] = []
  constructor(
    public vhAuth: VhAuth
  ) { }

  ngOnInit(): void {
    this.initData()
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  initData() {
    // nhn vin
    this.employeeList = this.vhAuth.getlocalEmployees();
  }

  changeEmployee(ev) {
    this.invoices.setIdEmployee(ev)
  }
}
